"use client";
import { useState } from 'react';
import { Globe } from 'lucide-react';

export default function ProjectCard({ title, description, category, details, githubLink }) {
  const [isFlipped, setIsFlipped] = useState(false);

  return (
    <div
      className="group h-[400px] w-80 [perspective:1000px] cursor-pointer hover:scale-[1.02] transition-all shadow-lg"
      onClick={() => setIsFlipped(!isFlipped)}
    >
      <div
        className={`relative h-full w-full rounded-xl transition-all duration-700 [transform-style:preserve-3d] ${isFlipped ? '[transform:rotateY(180deg)]' : ''
          }`}
      >
        {/* FRONT SIDE */}
        <div className="absolute inset-0 rounded-2xl bg-slate-900 border border-brand-accent/30 p-8 shadow-lg [backface-visibility:hidden] flex flex-col justify-between">
          <span className="text-brand-accent font-bold uppercase tracking-widest text-xs">
            {category}
          </span>
          <h3 className="text-white text-2xl font-bold mt-2">{title}</h3>
          <p className="text-slate-300 mt-4">{description}</p>
          <div className='flex'>
            <a href={githubLink} className='flex'>
              <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="white" strokeWidth="2" stroke-linecap="round" stroke-linejoin="round" className="lucide lucide-github-icon lucide-github"><path d="M15 22v-4a4.8 4.8 0 0 0-1-3.5c3 0 6-2 6-5.5.08-1.25-.27-2.48-1-3.5.28-1.15.28-2.35 0-3.5 0 0-1 0-3 1.5-2.64-.5-5.36-.5-8 0C6 2 5 2 5 2c-.3 1.15-.3 2.35 0 3.5A5.403 5.403 0 0 0 4 9c0 3.5 3 5.5 6 5.5-.39.49-.68 1.05-.85 1.65-.17.6-.22 1.23-.15 1.85v4" /><path d="M9 18c-4.51 2-5-2-7-2" /></svg>
            </a>
            <a>
              
            </a>
          </div>
        </div>

        {/* BACK SIDE */}
        <div className="absolute inset-0 rounded-2xl bg-slate-900 border border-brand-accent/30 p-8 text-white shadow-xl [transform:rotateY(180deg)] [backface-visibility:hidden] hover:shadow-2xl flex flex-col justify-between flex flex-col justify-between">
          <h4 className="text-brand-accent font-bold mb-2">Key Outcome</h4>
          <p className="text-slate-300">{details}</p>
          <a href={githubLink} className='flex'>
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" stroke-linecap="round" stroke-linejoin="round" className="lucide lucide-github-icon lucide-github"><path d="M15 22v-4a4.8 4.8 0 0 0-1-3.5c3 0 6-2 6-5.5.08-1.25-.27-2.48-1-3.5.28-1.15.28-2.35 0-3.5 0 0-1 0-3 1.5-2.64-.5-5.36-.5-8 0C6 2 5 2 5 2c-.3 1.15-.3 2.35 0 3.5A5.403 5.403 0 0 0 4 9c0 3.5 3 5.5 6 5.5-.39.49-.68 1.05-.85 1.65-.17.6-.22 1.23-.15 1.85v4" /><path d="M9 18c-4.51 2-5-2-7-2" /></svg>
            <span className="ml-2 text-brand-accent underline">View Code</span>
          </a>
        </div>
      </div>
    </div>
  );
}